import React, { useState, useEffect, useRef } from 'react';
import { 
  Box, 
  Typography, 
  Grid, 
  Card, 
  CardContent, 
  CardMedia, 
  CardActions,
  CardHeader,
  Button,
  IconButton,
  Skeleton,
  Alert,
  Snackbar,
  Divider,
  Tabs,
  Tab,
  Paper,
  useTheme,
  Chip
} from '@mui/material';
import { alpha } from '@mui/material/styles';
import axios from 'axios';

// Icons
import DeleteIcon from '@mui/icons-material/Delete';
import FullscreenIcon from '@mui/icons-material/Fullscreen';
import DownloadIcon from '@mui/icons-material/Download';
import TrendingUpIcon from '@mui/icons-material/TrendingUp';
import TrendingDownIcon from '@mui/icons-material/TrendingDown';
import ShowChartIcon from '@mui/icons-material/ShowChart';
import ElectricCarIcon from '@mui/icons-material/ElectricCar';
import DirectionsCarIcon from '@mui/icons-material/DirectionsCar';
import CompareArrowsIcon from '@mui/icons-material/CompareArrows';
import SpeedIcon from '@mui/icons-material/Speed';

// Components
import EnhancedChart from '../components/common/EnhancedChart';
import ChartContainer from '../components/common/ChartContainer';

const Dashboard = () => {
  const theme = useTheme();
  const [pinnedCharts, setPinnedCharts] = useState([]);
  const [evTopSellers, setEvTopSellers] = useState(null);
  const [iceTopSellers, setIceTopSellers] = useState(null);
  const [marketTrends, setMarketTrends] = useState(null);
  const [loading, setLoading] = useState({
    pinnedCharts: true,
    insights: true
  });
  const [error, setError] = useState(null);
  const [snackbar, setSnackbar] = useState({ open: false, message: '', severity: 'success' });
  const [activeTab, setActiveTab] = useState(0);
  
  // Chart refs for the predefined analytics charts
  const evChartRef = useRef(null);
  const iceChartRef = useRef(null);
  const trendsChartRef = useRef(null);
  const comparisonChartRef = useRef(null);
  
  useEffect(() => {
    fetchPinnedCharts();
    fetchDashboardData();
  }, []);
  
  const fetchPinnedCharts = async () => {
    try {
      setLoading(prev => ({ ...prev, pinnedCharts: true }));
      const response = await axios.get('/api/dashboard/pinned');
      setPinnedCharts(response.data);
    } catch (err) {
      console.error('Error fetching pinned charts:', err);
      setError('Failed to load pinned charts');
    } finally {
      setLoading(prev => ({ ...prev, pinnedCharts: false }));
    }
  };
  
  const fetchDashboardData = async () => {
    try {
      setLoading(prev => ({ ...prev, insights: true }));
      
      // Fetch EV top sellers data
      const evResponse = await axios.get('/api/vehicles/ev-analytics');
      setEvTopSellers(evResponse.data);
      
      // Fetch ICE top sellers data
      const iceResponse = await axios.get('/api/vehicles/ice-analytics');
      setIceTopSellers(iceResponse.data);
      
      // Fetch market trends data
      const trendsResponse = await axios.get('/api/vehicles/market-trends');
      setMarketTrends(trendsResponse.data || generateFallbackMarketTrends());
      
    } catch (err) {
      console.error('Error fetching dashboard data:', err);
      // Generate fallback data to prevent UI breaks
      setEvTopSellers(generateFallbackEvData());
      setIceTopSellers(generateFallbackIceData());
      setMarketTrends(generateFallbackMarketTrends());
    } finally {
      setLoading(prev => ({ ...prev, insights: false }));
    }
  };
  
  // Generate fallback data if API fails
  const generateFallbackEvData = () => {
    return {
      topSellers: [
        { maker: 'Tesla', model: 'Model Y', sales: 8700 },
        { maker: 'Tesla', model: 'Model 3', sales: 7200 },
        { maker: 'BYD', model: 'Atto 3', sales: 5400 },
        { maker: 'MG', model: 'ZS EV', sales: 4800 },
        { maker: 'Tata', model: 'Nexon EV', sales: 4200 }
      ],
      monthlyData: [
        { month: 'Jan', sales: 14500 },
        { month: 'Feb', sales: 15200 },
        { month: 'Mar', sales: 16800 },
        { month: 'Apr', sales: 18400 },
        { month: 'May', sales: 19700 }
      ],
      totalSales: 84600,
      growth: 37.8
    };
  };
  
  const generateFallbackIceData = () => {
    return {
      topSellers: [
        { maker: 'Maruti Suzuki', model: 'Swift', sales: 15400 },
        { maker: 'Hyundai', model: 'Creta', sales: 14200 },
        { maker: 'Tata', model: 'Nexon', sales: 13800 },
        { maker: 'Maruti Suzuki', model: 'Baleno', sales: 12600 },
        { maker: 'Mahindra', model: 'Bolero', sales: 11200 }
      ],
      monthlyData: [
        { month: 'Jan', sales: 112000 },
        { month: 'Feb', sales: 108000 },
        { month: 'Mar', sales: 105000 },
        { month: 'Apr', sales: 102000 },
        { month: 'May', sales: 99000 }
      ],
      totalSales: 526000,
      growth: -3.2
    };
  };
  
  const generateFallbackMarketTrends = () => {
    return {
      evShare: [
        { month: 'Jan', share: 4.2 },
        { month: 'Feb', share: 4.5 },
        { month: 'Mar', share: 4.8 },
        { month: 'Apr', share: 5.2 },
        { month: 'May', share: 5.6 }
      ],
      iceShare: [
        { month: 'Jan', share: 95.8 },
        { month: 'Feb', share: 95.5 },
        { month: 'Mar', share: 95.2 },
        { month: 'Apr', share: 94.8 },
        { month: 'May', share: 94.4 }
      ],
      comparisonData: [
        { year: '2019', ev: 2.1, ice: 97.9 },
        { year: '2020', ev: 2.8, ice: 97.2 },
        { year: '2021', ev: 3.5, ice: 96.5 },
        { year: '2022', ev: 4.3, ice: 95.7 },
        { year: '2023', ev: 5.6, ice: 94.4 }
      ]
    };
  };
  
  const handleDeleteChart = async (id) => {
    try {
      await axios.delete(`/api/dashboard/pin/${id}`);
      setPinnedCharts(prevCharts => prevCharts.filter(chart => chart._id !== id));
      setSnackbar({
        open: true,
        message: 'Chart removed from dashboard',
        severity: 'success'
      });
    } catch (err) {
      console.error('Error removing chart:', err);
      setSnackbar({
        open: true,
        message: 'Failed to remove chart',
        severity: 'error'
      });
    }
  };
  
  const handleDownloadChart = (chart) => {
    // Create a temporary link element to download the image
    const link = document.createElement('a');
    link.href = chart.imageUrl;
    link.download = `${chart.title.replace(/\s+/g, '-')}.png`;
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
    
    setSnackbar({
      open: true,
      message: 'Chart downloaded',
      severity: 'success'
    });
  };
  
  const handleCloseSnackbar = () => {
    setSnackbar({ ...snackbar, open: false });
  };
  
  const handleTabChange = (event, newValue) => {
    setActiveTab(newValue);
  };
  
  // Generate chart data for EV top sellers
  const getEvTopSellersChartData = () => {
    if (!evTopSellers) return null;
    
    return {
      labels: evTopSellers.topSellers.map(item => `${item.maker} ${item.model}`),
      datasets: [
        {
          label: 'Sales',
          data: evTopSellers.topSellers.map(item => item.sales),
          backgroundColor: theme.palette.primary.main,
          borderColor: theme.palette.primary.dark,
        }
      ]
    };
  };
  
  // Generate chart data for ICE top sellers
  const getIceTopSellersChartData = () => {
    if (!iceTopSellers) return null;
    
    return {
      labels: iceTopSellers.topSellers.map(item => `${item.maker} ${item.model}`),
      datasets: [
        {
          label: 'Sales',
          data: iceTopSellers.topSellers.map(item => item.sales),
          backgroundColor: theme.palette.secondary.main,
          borderColor: theme.palette.secondary.dark,
        }
      ]
    };
  };
  
  // Generate chart data for market trends
  const getMarketTrendsChartData = () => {
    if (!marketTrends) return null;
    
    return {
      labels: marketTrends.evShare.map(item => item.month),
      datasets: [
        {
          label: 'EV Market Share (%)',
          data: marketTrends.evShare.map(item => item.share),
          borderColor: theme.palette.primary.main,
          backgroundColor: alpha(theme.palette.primary.main, 0.1),
          fill: true,
          tension: 0.4
        },
        {
          label: 'ICE Market Share (%)',
          data: marketTrends.iceShare.map(item => item.share),
          borderColor: theme.palette.secondary.main,
          backgroundColor: alpha(theme.palette.secondary.main, 0.1),
          fill: true,
          tension: 0.4
        }
      ]
    };
  };
  
  // Generate chart data for EV vs ICE comparison
  const getComparisonChartData = () => {
    if (!marketTrends?.comparisonData) return null;
    
    return {
      labels: marketTrends.comparisonData.map(item => item.year),
      datasets: [
        {
          label: 'EV Share (%)',
          data: marketTrends.comparisonData.map(item => item.ev),
          backgroundColor: theme.palette.primary.main,
          borderColor: theme.palette.primary.dark,
          stack: 'Stack 0',
        },
        {
          label: 'ICE Share (%)',
          data: marketTrends.comparisonData.map(item => item.ice),
          backgroundColor: theme.palette.secondary.main,
          borderColor: theme.palette.secondary.dark,
          stack: 'Stack 0',
        }
      ]
    };
  };
  
  // Statistical summary cards
  const renderStatCards = () => {
    const statItems = [
      {
        title: 'EV Sales',
        value: evTopSellers?.totalSales?.toLocaleString() || '84,600',
        change: evTopSellers?.growth || 37.8,
        color: theme.palette.primary.main,
        icon: <ElectricCarIcon />,
      },
      {
        title: 'ICE Sales',
        value: iceTopSellers?.totalSales?.toLocaleString() || '526,000',
        change: iceTopSellers?.growth || -3.2,
        color: theme.palette.secondary.main,
        icon: <DirectionsCarIcon />,
      },
      {
        title: 'EV Market Share',
        value: `${marketTrends?.evShare?.[marketTrends.evShare.length - 1]?.share || 5.6}%`,
        change: 1.3,
        color: theme.palette.success.main,
        icon: <ShowChartIcon />,
      },
      {
        title: 'Market Growth',
        value: '8.2%',
        change: 2.1,
        color: theme.palette.info.main,
        icon: <SpeedIcon />,
      },
    ];
    
    return (
      <Grid container spacing={2}>
        {statItems.map((stat, index) => (
          <Grid item xs={12} sm={6} md={3} key={index}>
            <Card 
              sx={{ 
                height: '100%',
                border: `1px solid ${alpha(stat.color, 0.2)}`,
                boxShadow: `0 4px 12px ${alpha(stat.color, 0.08)}`,
                transition: 'all 0.3s ease',
                '&:hover': {
                  boxShadow: `0 6px 16px ${alpha(stat.color, 0.12)}`,
                  transform: 'translateY(-2px)'
                }
              }}
            >
              <CardContent sx={{ p: 2, pb: '16px !important' }}>
                <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 1 }}>
                  <Typography variant="subtitle2" color="text.secondary">
                    {stat.title}
                  </Typography>
                  <Box 
                    sx={{ 
                      p: 1, 
                      borderRadius: '50%', 
                      display: 'flex',
                      bgcolor: alpha(stat.color, 0.1),
                      color: stat.color
                    }}
                  >
                    {stat.icon}
                  </Box>
                </Box>
                <Typography variant="h5" sx={{ fontWeight: 600, mb: 0.5 }}>
                  {stat.value}
                </Typography>
                <Box 
                  sx={{ 
                    display: 'flex', 
                    alignItems: 'center',
                    color: stat.change >= 0 ? theme.palette.success.main : theme.palette.error.main
                  }}
                >
                  {stat.change >= 0 ? 
                    <TrendingUpIcon fontSize="small" /> : 
                    <TrendingDownIcon fontSize="small" />
                  }
                  <Typography variant="body2" sx={{ ml: 0.5 }}>
                    {Math.abs(stat.change)}%
                  </Typography>
                </Box>
              </CardContent>
            </Card>
          </Grid>
        ))}
      </Grid>
    );
  };
  
  return (
    <Box sx={{ 
      height: '100vh', 
      display: 'flex', 
      flexDirection: 'column',
      overflow: 'hidden'
    }}>
      <Box sx={{ 
        position: 'sticky', 
        top: 0, 
        zIndex: 10,
        backgroundColor: theme.palette.background.default,
        borderBottom: `1px solid ${alpha(theme.palette.divider, 0.1)}`
      }}>
        <Typography variant="h5" sx={{ px: 2, pt: 2, pb: 0 }}>
          Dashboard
        </Typography>
        <Typography variant="body2" color="text.secondary" sx={{ px: 2, pb: 2 }}>
          Analytics overview and insights
        </Typography>
      </Box>
      
      <Box sx={{ 
        flexGrow: 1, 
        overflow: 'auto',
        px: 2,
        pt: 2,
        pb: 4,
        '::-webkit-scrollbar': {
          width: '8px'
        },
        '::-webkit-scrollbar-track': {
          background: 'transparent'
        },
        '::-webkit-scrollbar-thumb': {
          background: alpha(theme.palette.primary.main, 0.1),
          borderRadius: '4px'
        },
        '::-webkit-scrollbar-thumb:hover': {
          background: alpha(theme.palette.primary.main, 0.2)
        }
      }}>
        {/* Key Stats Section */}
        <Box sx={{ mb: 3 }}>
          <Typography variant="h6" sx={{ mb: 2, fontWeight: 600, display: 'flex', alignItems: 'center' }}>
            <ShowChartIcon sx={{ mr: 1, color: theme.palette.primary.main }} />
            Key Insights
          </Typography>
          
          {loading.insights ? (
            <Grid container spacing={2}>
              {[1, 2, 3, 4].map((item) => (
                <Grid item xs={12} sm={6} md={3} key={item}>
                  <Skeleton variant="rectangular" height={120} />
                </Grid>
              ))}
            </Grid>
          ) : (
            renderStatCards()
          )}
        </Box>
        
        {/* Analytics Charts Section */}
        <Box sx={{ mb: 3 }}>
          <Typography variant="h6" sx={{ mb: 2, fontWeight: 600, display: 'flex', alignItems: 'center' }}>
            <CompareArrowsIcon sx={{ mr: 1, color: theme.palette.primary.main }} />
            Market Analysis
          </Typography>
          
          <Grid container spacing={3}>
            <Grid item xs={12} md={6}>
              <ChartContainer
                title="Top EV Sellers"
                isLoading={loading.insights}
                height="280px"
                chartRef={!loading.insights ? evChartRef : null}
                chartId="dashboard-ev-top-sellers"
                chartType="bar"
              >
                {evTopSellers && (
                  <EnhancedChart
                    ref={evChartRef}
                    type="bar"
                    data={getEvTopSellersChartData()}
                    colorPalette="ev"
                  />
                )}
              </ChartContainer>
            </Grid>
            
            <Grid item xs={12} md={6}>
              <ChartContainer
                title="Top ICE Sellers"
                isLoading={loading.insights}
                height="280px"
                chartRef={!loading.insights ? iceChartRef : null}
                chartId="dashboard-ice-top-sellers"
                chartType="bar"
              >
                {iceTopSellers && (
                  <EnhancedChart
                    ref={iceChartRef}
                    type="bar"
                    data={getIceTopSellersChartData()}
                    colorPalette="ice"
                  />
                )}
              </ChartContainer>
            </Grid>
            
            <Grid item xs={12} md={6}>
              <ChartContainer
                title="Market Share Trends"
                isLoading={loading.insights}
                height="280px"
                chartRef={!loading.insights ? trendsChartRef : null}
                chartId="dashboard-market-trends"
                chartType="line"
              >
                {marketTrends && (
                  <EnhancedChart
                    ref={trendsChartRef}
                    type="line"
                    data={getMarketTrendsChartData()}
                    colorPalette="combined"
                  />
                )}
              </ChartContainer>
            </Grid>
            
            <Grid item xs={12} md={6}>
              <ChartContainer
                title="EV vs ICE - Yearly Comparison"
                isLoading={loading.insights}
                height="280px"
                chartRef={!loading.insights ? comparisonChartRef : null}
                chartId="dashboard-yearly-comparison"
                chartType="bar"
              >
                {marketTrends && (
                  <EnhancedChart
                    ref={comparisonChartRef}
                    type="bar"
                    data={getComparisonChartData()}
                    options={{
                      scales: {
                        x: {
                          stacked: true,
                        },
                        y: {
                          stacked: true,
                          max: 100
                        }
                      }
                    }}
                    colorPalette="combined"
                  />
                )}
              </ChartContainer>
            </Grid>
          </Grid>
        </Box>
        
        {/* Pinned Charts Section */}
        <Box>
          <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 2 }}>
            <Typography variant="h6" sx={{ fontWeight: 600, display: 'flex', alignItems: 'center' }}>
              <ElectricCarIcon sx={{ mr: 1, color: theme.palette.primary.main }} />
              Your Pinned Charts
            </Typography>
            
            <Button 
              size="small" 
              variant="outlined" 
              color="primary"
              component="a"
              href="/custom-dashboard"
            >
              View Live Charts
            </Button>
          </Box>
          
          {loading.pinnedCharts ? (
            <Grid container spacing={3}>
              {[1, 2, 3].map((item) => (
                <Grid item xs={12} sm={6} md={4} key={item}>
                  <Card>
                    <Skeleton variant="rectangular" height={200} />
                    <CardContent>
                      <Skeleton width="80%" />
                      <Skeleton width="40%" />
                    </CardContent>
                  </Card>
                </Grid>
              ))}
            </Grid>
          ) : error ? (
            <Alert severity="error" sx={{ mb: 2 }}>
              {error}
            </Alert>
          ) : pinnedCharts.length === 0 ? (
            <Box 
              sx={{ 
                display: 'flex', 
                flexDirection: 'column', 
                alignItems: 'center', 
                justifyContent: 'center',
                py: 6,
                textAlign: 'center',
                bgcolor: alpha(theme.palette.primary.main, 0.05),
                borderRadius: 2,
                border: `1px dashed ${alpha(theme.palette.primary.main, 0.2)}`
              }}
            >
              <Typography variant="h6" color="text.secondary" gutterBottom>
                No pinned charts yet
              </Typography>
              <Typography variant="body2" color="text.secondary" sx={{ mb: 2 }}>
                Pin charts from the analytics pages to see them here
              </Typography>
              <Button 
                variant="outlined" 
                color="primary"
                size="small"
                component="a"
                href="/ev-analytics"
              >
                Go to Analytics
              </Button>
            </Box>
          ) : (
            <Grid container spacing={3}>
              {pinnedCharts.map((chart) => (
                <Grid item xs={12} sm={6} md={4} key={chart._id}>
                  <Card 
                    sx={{ 
                      height: '100%',
                      display: 'flex',
                      flexDirection: 'column',
                      transition: 'all 0.3s ease',
                      '&:hover': {
                        boxShadow: '0 8px 24px rgba(0, 0, 0, 0.12)'
                      }
                    }}
                  >
                    <CardHeader
                      title={
                        <Typography variant="subtitle1" sx={{ fontWeight: 600 }}>
                          {chart.title}
                        </Typography>
                      }
                      subheader={new Date(chart.createdAt).toLocaleDateString()}
                      action={
                        <IconButton 
                          size="small" 
                          color="error"
                          onClick={() => handleDeleteChart(chart._id)}
                          aria-label="delete"
                        >
                          <DeleteIcon fontSize="small" />
                        </IconButton>
                      }
                      sx={{ pb: 0 }}
                    />
                    <CardContent sx={{ pt: 1, flexGrow: 1 }}>
                      <Chip 
                        label={chart.chartType} 
                        size="small" 
                        sx={{ 
                          bgcolor: alpha(theme.palette.primary.main, 0.1),
                          color: theme.palette.primary.main,
                          mb: 1
                        }} 
                      />
                      <CardMedia
                        component="img"
                        height={180}
                        image={chart.imageUrl}
                        alt={chart.title}
                        sx={{ borderRadius: 1, mt: 1 }}
                      />
                    </CardContent>
                    <CardActions sx={{ justifyContent: 'flex-end', pt: 0 }}>
                      <IconButton 
                        size="small" 
                        onClick={() => handleDownloadChart(chart)}
                        aria-label="download"
                      >
                        <DownloadIcon fontSize="small" />
                      </IconButton>
                    </CardActions>
                  </Card>
                </Grid>
              ))}
            </Grid>
          )}
        </Box>
      </Box>
      
      <Snackbar
        open={snackbar.open}
        autoHideDuration={4000}
        onClose={handleCloseSnackbar}
        anchorOrigin={{ vertical: 'bottom', horizontal: 'right' }}
      >
        <Alert 
          onClose={handleCloseSnackbar} 
          severity={snackbar.severity} 
          variant="filled"
        >
          {snackbar.message}
        </Alert>
      </Snackbar>
    </Box>
  );
};

export default Dashboard; 