import React, { useState, useEffect } from 'react';
import { 
  Paper, 
  Box, 
  Typography, 
  FormControl, 
  InputLabel, 
  Select, 
  MenuItem,
  Grid,
  Button,
  Accordion,
  AccordionSummary,
  AccordionDetails,
  Chip,
  Divider,
  IconButton,
} from '@mui/material';
import FilterAltIcon from '@mui/icons-material/FilterAlt';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import RefreshIcon from '@mui/icons-material/Refresh';
import axios from 'axios';
import { alpha } from '@mui/material/styles';
import { useTheme } from '@mui/material/styles';

const FilterPanel = ({ 
  onFilterChange, 
  defaultFilters = {}, 
  showMakerFilter = true, 
  showTypeFilter = true,
  expanded = true,
  setExpanded = null
}) => {
  const [years, setYears] = useState([]);
  const [states, setStates] = useState([]);
  const [makers, setMakers] = useState([]);
  const [expandedInternal, setExpandedInternal] = useState(expanded);
  
  // Use either external or internal expanded state
  const isExpanded = setExpanded !== null ? expanded : expandedInternal;
  const setIsExpanded = setExpanded !== null ? setExpanded : setExpandedInternal;

  const [filters, setFilters] = useState({
    year: defaultFilters.year || '',
    state: defaultFilters.state || '',
    maker: defaultFilters.maker || '',
    vehicleType: defaultFilters.vehicleType || 'all',
    ...defaultFilters
  });

  const theme = useTheme();

  // Update expanded state if prop changes
  useEffect(() => {
    if (setExpanded !== null) {
      setExpandedInternal(expanded);
    }
  }, [expanded, setExpanded]);

  useEffect(() => {
    const fetchFilterOptions = async () => {
      try {
        const [yearsRes, statesRes, makersRes] = await Promise.all([
          axios.get('/api/years'),
          axios.get('/api/states'),
          axios.get('/api/makers')
        ]);

        setYears(yearsRes.data);
        setStates(statesRes.data);
        setMakers(makersRes.data);
      } catch (error) {
        console.error('Error fetching filter options:', error);
      }
    };

    fetchFilterOptions();
  }, []);

  const handleFilterChange = (e) => {
    const { name, value } = e.target;
    setFilters(prev => ({
      ...prev,
      [name]: value
    }));
  };

  const handleVehicleTypeChange = (e) => {
    const { value } = e.target;
    setFilters(prev => ({
      ...prev,
      vehicleType: value
    }));
  };

  const handleApplyFilters = () => {
    onFilterChange(filters);
    setIsExpanded(false); // Auto-collapse after applying filters
  };

  const handleResetFilters = () => {
    const resetFilters = {
      year: '',
      state: '',
      maker: '',
      vehicleType: 'all',
      ...defaultFilters
    };
    setFilters(resetFilters);
    onFilterChange(resetFilters);
  };

  const handleExpandChange = () => {
    setIsExpanded(!isExpanded);
  };

  return (
    <Paper 
      elevation={0} 
      sx={{ 
        mb: 0.5,
        mx: 2,
        borderRadius: '8px',
        overflow: 'hidden',
        border: '1px solid rgba(229, 231, 235, 1)',
        backgroundColor: 'background.paper',
      }}
    >
      <Accordion 
        expanded={isExpanded} 
        onChange={handleExpandChange}
        disableGutters
        elevation={0}
        sx={{ 
          '&:before': {
            display: 'none',
          }
        }}
      >
        <AccordionSummary
          expandIcon={<ExpandMoreIcon />}
          sx={{ 
            backgroundColor: 'background.paper', 
            color: 'text.primary',
            borderBottom: isExpanded ? `1px solid ${alpha(theme.palette.divider, 0.2)}` : 'none',
            '& .MuiAccordionSummary-expandIconWrapper': {
              color: theme.palette.text.secondary,
            },
            py: 0.5,
            px: 1.5
          }}
        >
          <Box sx={{ display: 'flex', alignItems: 'center' }}>
            <FilterAltIcon sx={{ mr: 1, color: theme.palette.primary.main, fontSize: '1.2rem' }} />
            <Typography variant="subtitle2" fontWeight={600}>
              Filters
            </Typography>
            
            {/* Show active filter count */}
            {Object.entries(filters).filter(([key, value]) => 
              value && 
              (Array.isArray(value) ? value.length > 0 : true)
            ).length > 0 && (
              <Chip 
                label={Object.entries(filters).filter(([key, value]) => 
                  value && 
                  (Array.isArray(value) ? value.length > 0 : true)
                ).length} 
                size="small" 
                color="primary"
                sx={{ ml: 1, height: 20, '& .MuiChip-label': { px: 1, fontSize: '0.75rem' } }}
              />
            )}
          </Box>
        </AccordionSummary>
        
        <AccordionDetails sx={{ p: 1.5 }}>
          <Grid container spacing={2}>
            <Grid item xs={12} sm={6} md={4}>
              <FormControl fullWidth size="small">
                <InputLabel id="year-label">Year</InputLabel>
                <Select
                  labelId="year-label"
                  id="year"
                  name="year"
                  value={filters.year}
                  label="Year"
                  onChange={handleFilterChange}
                >
                  <MenuItem value="">
                    <em>All Years</em>
                  </MenuItem>
                  {years.map(year => (
                    <MenuItem key={year} value={year}>{year}</MenuItem>
                  ))}
                </Select>
              </FormControl>
            </Grid>
            
            <Grid item xs={12} sm={6} md={4}>
              <FormControl fullWidth size="small">
                <InputLabel id="state-label">State</InputLabel>
                <Select
                  labelId="state-label"
                  id="state"
                  name="state"
                  value={filters.state}
                  label="State"
                  onChange={handleFilterChange}
                >
                  <MenuItem value="">
                    <em>All States</em>
                  </MenuItem>
                  {states.map(state => (
                    <MenuItem key={state} value={state}>{state}</MenuItem>
                  ))}
                </Select>
              </FormControl>
            </Grid>
            
            {showMakerFilter && (
              <Grid item xs={12} sm={6} md={4}>
                <FormControl fullWidth size="small">
                  <InputLabel id="maker-label">Maker</InputLabel>
                  <Select
                    labelId="maker-label"
                    id="maker"
                    name="maker"
                    value={filters.maker}
                    label="Maker"
                    onChange={handleFilterChange}
                  >
                    <MenuItem value="">
                      <em>All Makers</em>
                    </MenuItem>
                    {makers.map(maker => (
                      <MenuItem key={maker} value={maker}>{maker}</MenuItem>
                    ))}
                  </Select>
                </FormControl>
              </Grid>
            )}
            
            {showTypeFilter && (
              <Grid item xs={12} sm={6} md={4}>
                <FormControl fullWidth size="small">
                  <InputLabel id="vehicle-type-label">Vehicle Type</InputLabel>
                  <Select
                    labelId="vehicle-type-label"
                    id="vehicleType"
                    name="vehicleType"
                    value={filters.vehicleType}
                    label="Vehicle Type"
                    onChange={handleVehicleTypeChange}
                  >
                    <MenuItem value="all">All Types</MenuItem>
                    <MenuItem value="ev">Electric Vehicles</MenuItem>
                    <MenuItem value="ice">ICE Vehicles</MenuItem>
                  </Select>
                </FormControl>
              </Grid>
            )}
          </Grid>
          
          <Box sx={{ display: 'flex', justifyContent: 'flex-end', mt: 2, gap: 1 }}>
            <Button 
              variant="outlined" 
              startIcon={<RefreshIcon />}
              onClick={handleResetFilters}
              size="small"
            >
              Reset
            </Button>
            <Button 
              variant="contained" 
              disableElevation
              onClick={handleApplyFilters}
              size="small"
            >
              Apply
            </Button>
          </Box>
        </AccordionDetails>
      </Accordion>
    </Paper>
  );
};

export default FilterPanel; 