import React, { useState, useEffect, useRef } from 'react';
import { 
  Typography, 
  Box, 
  Grid, 
  Table, 
  TableBody, 
  TableCell, 
  TableContainer, 
  TableHead, 
  TableRow, 
  Paper,
  TablePagination,
  Button,
  Chip,
  IconButton, 
  useTheme,
  Autocomplete,
  TextField,
  Divider,
  Card,
  CardContent,
  CardHeader,
  Tab,
  Tabs,
  Tooltip
} from '@mui/material';
import { alpha } from '@mui/material/styles';
import axios from 'axios';
import { Bar, Line } from 'react-chartjs-2';
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  BarElement,
  LineElement,
  PointElement,
  Title,
  Tooltip as ChartTooltip,
  Legend
} from 'chart.js';

// Icons
import CompareArrowsIcon from '@mui/icons-material/CompareArrows';
import ClearIcon from '@mui/icons-material/Clear';
import AddIcon from '@mui/icons-material/Add';
import InfoIcon from '@mui/icons-material/Info';
import BarChartIcon from '@mui/icons-material/BarChart';
import TimelineIcon from '@mui/icons-material/Timeline';
import DownloadIcon from '@mui/icons-material/Download';

// Components
import FilterPanel from '../components/common/FilterPanel';
import ChartContainer from '../components/common/ChartContainer';

// Register ChartJS components
ChartJS.register(
  CategoryScale,
  LinearScale,
  BarElement,
  LineElement,
  PointElement,
  Title,
  ChartTooltip,
  Legend
);

const EvAnalytics = () => {
  const theme = useTheme();
  const contentRef = useRef(null);
  const evMakersChartRef = useRef(null);
  const monthlyTrendChartRef = useRef(null);
  const makerMonthlyChartRef = useRef(null);
  const comparisonChartRef = useRef(null);
  
  const [filters, setFilters] = useState({
    year: '',
    state: '',
    maker: '',
    vehicleType: 'ev'
  });
  
  const [evData, setEvData] = useState([]);
  const [monthlyData, setMonthlyData] = useState([]);
  const [makerMonthlyData, setMakerMonthlyData] = useState([]);
  const [allMakers, setAllMakers] = useState([]);
  const [selectedMakers, setSelectedMakers] = useState([]);
  const [compareData, setCompareData] = useState([]);
  const [tabValue, setTabValue] = useState(0);
  const [viewType, setViewType] = useState('bar');
  
  const [isLoading, setIsLoading] = useState({
    evMakers: true,
    monthlyTrend: true,
    tableData: true,
    makerMonthly: true,
    comparison: true
  });
  
  // Pagination state
  const [page, setPage] = useState(0);
  const [rowsPerPage, setRowsPerPage] = useState(5);

  const [expanded, setExpanded] = useState(true); // Track filter panel expansion state

  // Fetch makers for comparison dropdown
  useEffect(() => {
    const fetchMakers = async () => {
      try {
        const response = await axios.get('/api/makers');
        // Filter for likely EV makers for better user experience
        const evMakers = response.data.filter(maker => 
          maker.toLowerCase().includes('electric') || 
          maker.toLowerCase().includes('ev') ||
          maker.toLowerCase().includes('battery') ||
          maker.toLowerCase().includes('ather') ||
          maker.toLowerCase().includes('ola')
        );
        setAllMakers(evMakers);
      } catch (error) {
        console.error('Error fetching makers:', error);
      }
    };
    
    fetchMakers();
  }, []);

  // Fetch data based on filters
  useEffect(() => {
    const fetchData = async () => {
      try {
        setIsLoading(prev => ({ 
          ...prev, 
          evMakers: true,
          monthlyTrend: true,
          tableData: true,
          makerMonthly: filters.maker ? true : prev.makerMonthly
        }));

        const requests = [
          axios.get('/api/sales/ev', { 
            params: { 
              year: filters.year, 
              state: filters.state
            } 
          }),
          axios.get('/api/sales/monthly', { 
            params: { 
              year: filters.year, 
              state: filters.state,
              type: 'ev'
            } 
          })
        ];
        
        // Add maker monthly data request if a maker is selected
        if (filters.maker) {
          requests.push(
            axios.get('/api/sales/maker-monthly', {
              params: {
                year: filters.year,
                state: filters.state,
                maker: filters.maker
              }
            })
          );
        }

        const responses = await Promise.all(requests);
        
        setEvData(responses[0].data);
        setMonthlyData(responses[1].data);
        
        if (filters.maker && responses[2]) {
          setMakerMonthlyData(responses[2].data);
        }
      } catch (error) {
        console.error('Error fetching data:', error);
      } finally {
        setIsLoading(prev => ({ 
          ...prev, 
          evMakers: false,
          monthlyTrend: false,
          tableData: false,
          makerMonthly: filters.maker ? false : prev.makerMonthly
        }));
      }
    };

    fetchData();
  }, [filters]);

  // Fetch comparison data when selected makers change
  useEffect(() => {
    const fetchComparisonData = async () => {
      if (selectedMakers.length === 0) {
        setCompareData([]);
        return;
      }
      
      try {
        setIsLoading(prev => ({ ...prev, comparison: true }));
        
        const response = await axios.get('/api/sales/compare-makers', {
          params: {
            year: filters.year,
            state: filters.state,
            makers: selectedMakers.join(',')
          }
        });
        
        setCompareData(response.data);
      } catch (error) {
        console.error('Error fetching comparison data:', error);
      } finally {
        setIsLoading(prev => ({ ...prev, comparison: false }));
      }
    };
    
    fetchComparisonData();
  }, [selectedMakers, filters.year, filters.state]);

  const handleFilterChange = (newFilters) => {
    setFilters(prevFilters => ({
      ...prevFilters,
      ...newFilters
    }));
    setPage(0);  // Reset to first page whenever filters change
    setExpanded(false); // Collapse filter panel after applying filters
    
    // Scroll to top of content
    setTimeout(() => {
      if (contentRef.current) {
        contentRef.current.scrollTop = 0;
      }
    }, 100);
  };


  const handleChangePage = (event, newPage) => {
    setPage(newPage);
  };

  const handleChangeRowsPerPage = (event) => {
    setRowsPerPage(parseInt(event.target.value, 10));
    setPage(0);
  };
  
  const handleTabChange = (event, newValue) => {
    setTabValue(newValue);
  };
  
  const handleViewTypeChange = (type) => {
    setViewType(type);
  };
  
  const handleAddMaker = (maker) => {
    if (maker && !selectedMakers.includes(maker)) {
      setSelectedMakers(prev => [...prev, maker]);
    }
  };
  
  const handleRemoveMaker = (maker) => {
    setSelectedMakers(prev => prev.filter(m => m !== maker));
  };

  // Prepare chart data
  const evMakersChartData = {
    labels: evData.slice(0, 10).map(item => item._id),
    datasets: [
      {
        label: 'EV Sales',
        data: evData.slice(0, 10).map(item => item.totalSales),
        backgroundColor: 'rgba(54, 162, 235, 0.6)',
        borderColor: 'rgba(54, 162, 235, 1)',
        borderWidth: 1
      }
    ]
  };

  const monthlyTrendChartData = {
    labels: monthlyData.map(item => item.month.substring(0, 3)),
    datasets: [
      {
        label: 'Monthly EV Sales',
        data: monthlyData.map(item => item.sales),
        fill: false,
        backgroundColor: 'rgba(75, 192, 192, 0.6)',
        borderColor: 'rgba(75, 192, 192, 1)',
        tension: 0.1
      }
    ]
  };
  
  // Chart data for individual maker monthly sales
  const makerMonthlyChartData = {
    labels: makerMonthlyData.map(item => item.shortMonth),
    datasets: [
      {
        label: `${filters.maker || 'Maker'} Monthly Sales`,
        data: makerMonthlyData.map(item => item.sales),
        fill: false,
        backgroundColor: 'rgba(153, 102, 255, 0.6)',
        borderColor: 'rgba(153, 102, 255, 1)',
        tension: 0.1
      }
    ]
  };
  
  // Prepare comparison chart data
  const comparisonChartData = {
    labels: ['JAN', 'FEB', 'MAR', 'APR', 'MAY', 'JUN', 'JUL', 'AUG', 'SEP', 'OCT', 'NOV', 'DEC'],
    datasets: compareData.map((makerData, index) => {
      // Generate a color based on index to differentiate makers
      const hue = (index * 137) % 360; // Using golden angle approximation for good distribution
      const color = `hsla(${hue}, 70%, 50%, 0.7)`;
      const borderColor = `hsla(${hue}, 70%, 45%, 1)`;
      
      return {
        label: makerData.maker,
        data: makerData.monthlyData.map(m => m.sales),
        fill: false,
        backgroundColor: color,
        borderColor: borderColor,
        tension: 0.1
      };
    })
  };

  // Function to optimize table data display
  const optimizeTableDisplay = () => {
    return evData
      .slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage)
      .map((row) => (
        <TableRow 
          key={row._id} 
          hover 
          sx={{ 
            '&:nth-of-type(odd)': { 
              backgroundColor: theme.palette.action.hover 
            },
            cursor: 'pointer',
            '&:hover': {
              backgroundColor: alpha(theme.palette.primary.light, 0.1)
            }
          }}
          onClick={() => handleFilterChange({ maker: row._id })}
        >
          <TableCell component="th" scope="row" sx={{ py: 1, fontSize: '0.875rem' }}>
            {row._id}
          </TableCell>
          <TableCell align="right" sx={{ py: 1, fontSize: '0.875rem' }}>{row.totalSales.toLocaleString()}</TableCell>
          <TableCell align="center" sx={{ py: 1 }}>
            <Button 
              size="small" 
              variant="outlined" 
              startIcon={<CompareArrowsIcon />}
              onClick={(e) => {
                e.stopPropagation();
                handleAddMaker(row._id);
                setTabValue(1); // Switch to comparison tab
              }}
            >
              Compare
            </Button>
          </TableCell>
        </TableRow>
      ));
  };

  // Add a function to export data as CSV
  const exportToCSV = (data, filename) => {
    // Prepare CSV content
    const headers = ["Rank", "Maker", "Total Sales"];
    const csvContent = [
      headers.join(","),
      ...data.map((row, index) => [
        index + 1,
        `"${row._id.replace(/"/g, '""')}"`, // Escape quotes in maker names
        row.totalSales
      ].join(","))
    ].join("\n");

    // Create download link
    const blob = new Blob([csvContent], { type: 'text/csv;charset=utf-8;' });
    const url = URL.createObjectURL(blob);
    const link = document.createElement('a');
    link.setAttribute('href', url);
    link.setAttribute('download', filename);
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
  };

  return (
    <Box sx={{ 
      height: '100vh', 
      display: 'flex', 
      flexDirection: 'column',
      overflow: 'hidden',
      pl: 0,
      ml: 0
    }}>
      <Box sx={{ 
        position: 'sticky', 
        top: 0, 
        zIndex: 10,
        backgroundColor: theme.palette.background.default,
        borderBottom: `1px solid ${alpha(theme.palette.divider, 0.1)}`
      }}>
        <Typography variant="h5" sx={{ px: 2, pt: 2, pb: 1 }}>
          EV Analytics
        </Typography>
        
        <Box>
          <FilterPanel 
            onFilterChange={handleFilterChange} 
            defaultFilters={{ vehicleType: 'ev' }}
            showTypeFilter={false}
            expanded={expanded}
            setExpanded={setExpanded}
          />
        </Box>
        
        <Box sx={{ px: 2, borderBottom: 1, borderColor: 'divider' }}>
          <Tabs 
            value={tabValue} 
            onChange={handleTabChange}
            indicatorColor="primary"
            textColor="primary"
            aria-label="EV analysis tabs"
          >
            <Tab label="Sales Overview" />
            <Tab label="Maker Comparison" />
          </Tabs>
        </Box>
      </Box>
      
      <Box 
        ref={contentRef}
        sx={{ 
          flexGrow: 1, 
          overflow: 'auto',
          px: 2,
          pt: 2,
          pb: 4,
          '::-webkit-scrollbar': {
            width: '8px'
          },
          '::-webkit-scrollbar-track': {
            background: 'transparent'
          },
          '::-webkit-scrollbar-thumb': {
            background: alpha(theme.palette.primary.main, 0.1),
            borderRadius: '4px'
          },
          '::-webkit-scrollbar-thumb:hover': {
            background: alpha(theme.palette.primary.main, 0.2)
          }
        }}
      >
        {tabValue === 0 && (
          <>
            <Grid container spacing={2}>
              {/* Top EV Makers Chart */}
              <Grid item xs={12} md={6}>
                <ChartContainer 
                  title="Top 10 EV Makers by Sales" 
                  isLoading={isLoading.evMakers}
                  chartRef={!isLoading.evMakers ? evMakersChartRef : null}
                  chartId="top-ev-makers"
                  chartType="bar"
                >
                  <Bar 
                    ref={evMakersChartRef}
                    data={evMakersChartData} 
                    options={{
                      responsive: true,
                      maintainAspectRatio: false,
                      plugins: {
                        legend: {
                          display: false,
                        },
                        tooltip: {
                          callbacks: {
                            label: function(context) {
                              return `Sales: ${context.raw.toLocaleString()}`;
                            }
                          }
                        }
                      },
                      scales: {
                        y: {
                          beginAtZero: true,
                          ticks: {
                            callback: function(value) {
                              return value.toLocaleString();
                            }
                          }
                        }
                      }
                    }} 
                  />
                </ChartContainer>
              </Grid>
              
              {/* Monthly Trend Chart */}
              <Grid item xs={12} md={6}>
                <ChartContainer 
                  title="Monthly EV Sales Trend" 
                  isLoading={isLoading.monthlyTrend}
                  chartRef={!isLoading.monthlyTrend ? monthlyTrendChartRef : null}
                  chartId="monthly-ev-trend"
                  chartType="line"
                >
                  <Line 
                    ref={monthlyTrendChartRef}
                    data={monthlyTrendChartData} 
                    options={{
                      responsive: true,
                      maintainAspectRatio: false,
                      plugins: {
                        legend: {
                          display: false,
                        },
                        tooltip: {
                          callbacks: {
                            label: function(context) {
                              return `Sales: ${context.raw.toLocaleString()}`;
                            }
                          }
                        }
                      },
                      scales: {
                        y: {
                          beginAtZero: true,
                          ticks: {
                            callback: function(value) {
                              return value.toLocaleString();
                            }
                          }
                        }
                      }
                    }} 
                  />
                </ChartContainer>
              </Grid>

              {/* All EV Makers Sales for Selected Year */}
              {filters.year && (
                <Grid item xs={12}>
                  <ChartContainer 
                    title={`All EV Makers Sales for ${filters.year}${filters.state ? ` in ${filters.state}` : ''}`}
                    isLoading={isLoading.evMakers}
                    height="500px"
                    actions={
                      <Tooltip title="Download as CSV">
                        <IconButton 
                          size="small"
                          onClick={() => exportToCSV(evData, `ev_makers_sales_${filters.year}${filters.state ? `_${filters.state}` : ''}.csv`)}
                          disabled={isLoading.evMakers || evData.length === 0}
                        >
                          <DownloadIcon />
                        </IconButton>
                      </Tooltip>
                    }
                  >
                    {evData.length > 0 ? (
                      <Box sx={{ 
                        maxHeight: '450px', 
                        overflow: 'auto',
                        '::-webkit-scrollbar': {
                          width: '8px',
                          height: '8px'
                        },
                        '::-webkit-scrollbar-track': {
                          background: 'transparent'
                        },
                        '::-webkit-scrollbar-thumb': {
                          background: alpha(theme.palette.primary.main, 0.1),
                          borderRadius: '4px'
                        },
                        '::-webkit-scrollbar-thumb:hover': {
                          background: alpha(theme.palette.primary.main, 0.2)
                        }
                      }}>
                        {/* Summary Stats */}
                        <Box sx={{ p: 2, backgroundColor: alpha(theme.palette.primary.light, 0.05), borderRadius: 1, mb: 2 }}>
                          <Grid container spacing={2}>
                            <Grid item xs={12} sm={4}>
                              <Typography variant="subtitle2" color="primary" gutterBottom>
                                Total EV Makers
                              </Typography>
                              <Typography variant="h6">
                                {evData.length}
                              </Typography>
                            </Grid>
                            <Grid item xs={12} sm={4}>
                              <Typography variant="subtitle2" color="primary" gutterBottom>
                                Total Sales
                              </Typography>
                              <Typography variant="h6">
                                {evData.reduce((sum, item) => sum + item.totalSales, 0).toLocaleString()}
                              </Typography>
                            </Grid>
                            <Grid item xs={12} sm={4}>
                              <Typography variant="subtitle2" color="primary" gutterBottom>
                                Top 10 Market Share
                              </Typography>
                              <Typography variant="h6">
                                {(evData.slice(0, 10).reduce((sum, item) => sum + item.totalSales, 0) / 
                                  evData.reduce((sum, item) => sum + item.totalSales, 0) * 100).toFixed(1)}%
                              </Typography>
                            </Grid>
                          </Grid>
                        </Box>
                        <TableContainer component={Paper} sx={{ boxShadow: 'none' }}>
                          <Table stickyHeader size="small" aria-label="All EV makers sales table">
                            <TableHead>
                              <TableRow>
                                <TableCell sx={{ fontWeight: 'bold', backgroundColor: theme.palette.primary.light, color: 'white' }}>Rank</TableCell>
                                <TableCell sx={{ fontWeight: 'bold', backgroundColor: theme.palette.primary.light, color: 'white' }}>Maker</TableCell>
                                <TableCell align="right" sx={{ fontWeight: 'bold', backgroundColor: theme.palette.primary.light, color: 'white' }}>Total Sales</TableCell>
                                <TableCell align="center" sx={{ fontWeight: 'bold', backgroundColor: theme.palette.primary.light, color: 'white' }}>Actions</TableCell>
                              </TableRow>
                            </TableHead>
                            <TableBody>
                              {evData.map((row, index) => (
                                <TableRow 
                                  key={row._id} 
                                  hover 
                                  sx={{ 
                                    '&:nth-of-type(odd)': { backgroundColor: theme.palette.action.hover },
                                    ...(index < 10 && { backgroundColor: alpha(theme.palette.success.light, 0.1) }),
                                    ...(index === 0 && { backgroundColor: alpha(theme.palette.success.main, 0.2) }),
                                    ...(index === 1 && { backgroundColor: alpha(theme.palette.success.main, 0.15) }),
                                    ...(index === 2 && { backgroundColor: alpha(theme.palette.success.main, 0.1) }),
                                    cursor: 'pointer',
                                    '&:hover': {
                                      backgroundColor: alpha(theme.palette.primary.light, 0.1)
                                    }
                                  }}
                                  onClick={() => handleFilterChange({ maker: row._id })}
                                >
                                  <TableCell component="th" scope="row" sx={{ py: 1, fontSize: '0.875rem' }}>
                                    {index + 1}{index < 10 && <Chip size="small" color="success" label="Top 10" sx={{ ml: 1 }} />}
                                  </TableCell>
                                  <TableCell component="th" scope="row" sx={{ py: 1, fontSize: '0.875rem' }}>
                                    {row._id}
                                  </TableCell>
                                  <TableCell align="right" sx={{ py: 1, fontSize: '0.875rem' }}>{row.totalSales.toLocaleString()}</TableCell>
                                  <TableCell align="center" sx={{ py: 1 }}>
                                    <Button 
                                      size="small" 
                                      variant="outlined" 
                                      startIcon={<CompareArrowsIcon />}
                                      onClick={(e) => {
                                        e.stopPropagation();
                                        handleAddMaker(row._id);
                                        setTabValue(1); // Switch to comparison tab
                                      }}
                                    >
                                      Compare
                                    </Button>
                                  </TableCell>
                                </TableRow>
                              ))}
                            </TableBody>
                          </Table>
                        </TableContainer>
                      </Box>
                    ) : (
                      <Typography variant="body2" color="text.secondary" align="center">
                        No data available for the selected filters.
                      </Typography>
                    )}
                  </ChartContainer>
                </Grid>
              )}
              
              {/* Maker Monthly Sales Chart - Only show if maker is selected */}
              {filters.maker && (
                <Grid item xs={12}>
                  <ChartContainer 
                    title={`Monthly Sales for ${filters.maker}`} 
                    isLoading={isLoading.makerMonthly}
                    chartRef={!isLoading.makerMonthly ? makerMonthlyChartRef : null}
                    chartId={`maker-${filters.maker.replace(/\s+/g, '-').toLowerCase()}`}
                    chartType="line"
                  >
                    <Line 
                      ref={makerMonthlyChartRef}
                      data={makerMonthlyChartData} 
                      options={{
                        responsive: true,
                        maintainAspectRatio: false,
                        plugins: {
                          legend: {
                            display: false,
                          },
                          tooltip: {
                            callbacks: {
                              label: function(context) {
                                return `Sales: ${context.raw.toLocaleString()}`;
                              }
                            }
                          }
                        },
                        scales: {
                          y: {
                            beginAtZero: true,
                            ticks: {
                              callback: function(value) {
                                return value.toLocaleString();
                              }
                            }
                          }
                        }
                      }} 
                    />
                  </ChartContainer>
                </Grid>
              )}
            </Grid>
            
            {/* Remove the redundant Data Table since we now have the All EV Makers Sales section */}
            {!filters.year && (
              <ChartContainer 
                title="EV Sales Data" 
                isLoading={isLoading.tableData}
                height="auto"
                sx={{ mt: 2 }}
              >
                <TableContainer sx={{ 
                  maxHeight: 300,
                  overflow: 'auto',
                  '::-webkit-scrollbar': {
                    width: '8px',
                    height: '8px'
                  },
                  '::-webkit-scrollbar-track': {
                    background: 'transparent'
                  },
                  '::-webkit-scrollbar-thumb': {
                    background: alpha(theme.palette.primary.main, 0.1),
                    borderRadius: '4px'
                  },
                  '::-webkit-scrollbar-thumb:hover': {
                    background: alpha(theme.palette.primary.main, 0.2)
                  }
                }}>
                  <Table stickyHeader size="small" aria-label="EV sales table">
                    <TableHead>
                      <TableRow>
                        <TableCell sx={{ fontWeight: 'bold', backgroundColor: theme.palette.primary.light, color: 'white' }}>Maker</TableCell>
                        <TableCell align="right" sx={{ fontWeight: 'bold', backgroundColor: theme.palette.primary.light, color: 'white' }}>Total Sales</TableCell>
                        <TableCell align="center" sx={{ fontWeight: 'bold', backgroundColor: theme.palette.primary.light, color: 'white' }}>Actions</TableCell>
                      </TableRow>
                    </TableHead>
                    <TableBody>
                      {optimizeTableDisplay()}
                    </TableBody>
                  </Table>
                </TableContainer>
                <TablePagination
                  rowsPerPageOptions={[5, 10, 25]}
                  component="div"
                  count={evData.length}
                  rowsPerPage={rowsPerPage}
                  page={page}
                  onPageChange={handleChangePage}
                  onRowsPerPageChange={handleChangeRowsPerPage}
                />
              </ChartContainer>
            )}
          </>
        )}
        
        {tabValue === 1 && (
          <Box>
            <Paper sx={{ p: 3, mb: 3 }} elevation={2}>
              <Typography variant="h6" gutterBottom>Compare EV Makers</Typography>
              <Grid container spacing={2} sx={{ mb: 2 }}>
                <Grid item xs={12} md={8}>
                  <Autocomplete
                    options={allMakers}
                    renderInput={(params) => (
                      <TextField 
                        {...params} 
                        label="Select EV Maker to Compare" 
                        variant="outlined" 
                        fullWidth
                      />
                    )}
                    onChange={(event, value) => handleAddMaker(value)}
                  />
                </Grid>
                <Grid item xs={12} md={4}>
                  <Box sx={{ display: 'flex', justifyContent: 'flex-end', height: '100%', alignItems: 'center' }}>
                    <Button 
                      variant="outlined"
                      startIcon={<BarChartIcon />}
                      onClick={() => handleViewTypeChange('bar')}
                      color={viewType === 'bar' ? 'primary' : 'inherit'}
                      sx={{ mr: 1 }}
                    >
                      Bar
                    </Button>
                    <Button 
                      variant="outlined"
                      startIcon={<TimelineIcon />}
                      onClick={() => handleViewTypeChange('line')}
                      color={viewType === 'line' ? 'primary' : 'inherit'}
                    >
                      Line
                    </Button>
                  </Box>
                </Grid>
              </Grid>
              
              <Box sx={{ mb: 2 }}>
                <Typography variant="subtitle2" gutterBottom>Selected Makers:</Typography>
                <Box sx={{ display: 'flex', flexWrap: 'wrap', gap: 1 }}>
                  {selectedMakers.length > 0 ? (
                    selectedMakers.map(maker => (
                      <Chip 
                        key={maker}
                        label={maker}
                        onDelete={() => handleRemoveMaker(maker)}
                        color="primary"
                        variant="outlined"
                      />
                    ))
                  ) : (
                    <Typography variant="body2" color="text.secondary">
                      No makers selected. Select makers from the dropdown above to compare.
                    </Typography>
                  )}
                </Box>
              </Box>
            </Paper>
            
            {selectedMakers.length > 0 && (
              <ChartContainer 
                title="EV Makers Monthly Sales Comparison" 
                isLoading={isLoading.comparison}
                height="500px"
                chartRef={!isLoading.comparison ? comparisonChartRef : null}
                chartId="ev-makers-comparison"
                chartType={viewType}
              >
                {viewType === 'bar' ? (
                  <Bar 
                    ref={comparisonChartRef}
                    data={comparisonChartData}
                    options={{
                      responsive: true,
                      maintainAspectRatio: false,
                      plugins: {
                        legend: {
                          position: 'top',
                        },
                        tooltip: {
                          callbacks: {
                            label: function(context) {
                              return `${context.dataset.label}: ${context.raw.toLocaleString()}`;
                            }
                          }
                        }
                      },
                      scales: {
                        y: {
                          beginAtZero: true,
                          ticks: {
                            callback: function(value) {
                              return value.toLocaleString();
                            }
                          }
                        }
                      }
                    }}
                  />
                ) : (
                  <Line 
                    ref={comparisonChartRef}
                    data={comparisonChartData}
                    options={{
                      responsive: true,
                      maintainAspectRatio: false,
                      plugins: {
                        legend: {
                          position: 'top',
                        },
                        tooltip: {
                          callbacks: {
                            label: function(context) {
                              return `${context.dataset.label}: ${context.raw.toLocaleString()}`;
                            }
                          }
                        }
                      },
                      scales: {
                        y: {
                          beginAtZero: true,
                          ticks: {
                            callback: function(value) {
                              return value.toLocaleString();
                            }
                          }
                        }
                      }
                    }}
                  />
                )}
              </ChartContainer>
            )}
            
            {/* Comparison Data Table */}
            {selectedMakers.length > 0 && (
              <ChartContainer 
                title="Comparison Data" 
                isLoading={isLoading.comparison}
                height="auto"
              >
                <TableContainer component={Paper} sx={{ maxHeight: 440, boxShadow: 'none' }}>
                  <Table stickyHeader aria-label="comparison table">
                    <TableHead>
                      <TableRow>
                        <TableCell sx={{ fontWeight: 'bold', backgroundColor: theme.palette.primary.light, color: 'white' }}>Maker</TableCell>
                        <TableCell align="right" sx={{ fontWeight: 'bold', backgroundColor: theme.palette.primary.light, color: 'white' }}>Total Sales</TableCell>
                        <TableCell align="right" sx={{ fontWeight: 'bold', backgroundColor: theme.palette.primary.light, color: 'white' }}>Highest Month</TableCell>
                        <TableCell align="right" sx={{ fontWeight: 'bold', backgroundColor: theme.palette.primary.light, color: 'white' }}>Lowest Month</TableCell>
                        <TableCell align="center" sx={{ fontWeight: 'bold', backgroundColor: theme.palette.primary.light, color: 'white' }}>Actions</TableCell>
                      </TableRow>
                    </TableHead>
                    <TableBody>
                      {compareData.map((row) => {
                        // Calculate highest and lowest months
                        const monthlyData = row.monthlyData || [];
                        const highestMonth = [...monthlyData].sort((a, b) => b.sales - a.sales)[0] || {};
                        const lowestMonth = [...monthlyData].filter(m => m.sales > 0).sort((a, b) => a.sales - b.sales)[0] || {};
                        
                        return (
                          <TableRow 
                            key={row.maker} 
                            hover 
                            sx={{ 
                              '&:nth-of-type(odd)': { backgroundColor: theme.palette.action.hover }
                            }}
                          >
                            <TableCell component="th" scope="row">{row.maker}</TableCell>
                            <TableCell align="right">{row.totalSales.toLocaleString()}</TableCell>
                            <TableCell align="right">
                              {highestMonth.month ? `${highestMonth.month}: ${highestMonth.sales.toLocaleString()}` : 'N/A'}
                            </TableCell>
                            <TableCell align="right">
                              {lowestMonth.month ? `${lowestMonth.month}: ${lowestMonth.sales.toLocaleString()}` : 'N/A'}
                            </TableCell>
                            <TableCell align="center">
                              <IconButton 
                                size="small" 
                                color="error"
                                onClick={() => handleRemoveMaker(row.maker)}
                              >
                                <ClearIcon />
                              </IconButton>
                            </TableCell>
                          </TableRow>
                        );
                      })}
                    </TableBody>
                  </Table>
                </TableContainer>
              </ChartContainer>
            )}
          </Box>
        )}
      </Box>
    </Box>
  );
};

export default EvAnalytics; 