import React, { useState, useEffect, useRef } from 'react';
import { 
  Typography, 
  Box, 
  Grid, 
  Table, 
  TableBody, 
  TableCell, 
  TableContainer, 
  TableHead, 
  TableRow, 
  Paper,
  TablePagination,
  Button,
  Chip,
  IconButton,
  useTheme,
  Autocomplete,
  TextField,
  Divider,
  Card,
  CardContent,
  Tab,
  Tabs,
  Tooltip
} from '@mui/material';
import { alpha } from '@mui/material/styles';
import axios from 'axios';
import { Bar, Line } from 'react-chartjs-2';
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  BarElement,
  LineElement,
  PointElement,
  Title,
  Tooltip as ChartTooltip,
  Legend
} from 'chart.js';
import DownloadIcon from '@mui/icons-material/Download';

// Icons
import CompareArrowsIcon from '@mui/icons-material/CompareArrows';
import ClearIcon from '@mui/icons-material/Clear';
import AddIcon from '@mui/icons-material/Add';
import InfoIcon from '@mui/icons-material/Info';
import BarChartIcon from '@mui/icons-material/BarChart';
import TimelineIcon from '@mui/icons-material/Timeline';

// Components
import FilterPanel from '../components/common/FilterPanel';
import ChartContainer from '../components/common/ChartContainer';

// Register ChartJS components
ChartJS.register(
  CategoryScale,
  LinearScale,
  BarElement,
  LineElement,
  PointElement,
  Title,
  ChartTooltip,
  Legend
);

const IceAnalytics = () => {
  const theme = useTheme();
  const contentRef = useRef(null);
  const iceMakersChartRef = useRef(null);
  const monthlyTrendChartRef = useRef(null);
  const makerMonthlyChartRef = useRef(null);
  const comparisonChartRef = useRef(null);
  
  const [filters, setFilters] = useState({
    year: '',
    state: '',
    maker: '',
    vehicleType: 'ice'
  });
  
  const [expanded, setExpanded] = useState(true); // Track filter panel expansion state
  const [iceData, setIceData] = useState([]);
  const [monthlyData, setMonthlyData] = useState([]);
  const [makerMonthlyData, setMakerMonthlyData] = useState([]);
  const [allMakers, setAllMakers] = useState([]);
  const [selectedMakers, setSelectedMakers] = useState([]);
  const [compareData, setCompareData] = useState([]);
  const [tabValue, setTabValue] = useState(0);
  const [viewType, setViewType] = useState('bar');
  
  const [isLoading, setIsLoading] = useState({
    iceMakers: true,
    monthlyTrend: true,
    tableData: true,
    makerMonthly: true,
    comparison: true
  });
  
  // Pagination state
  const [page, setPage] = useState(0);
  const [rowsPerPage, setRowsPerPage] = useState(10);

  // Fetch makers for comparison dropdown
  useEffect(() => {
    const fetchMakers = async () => {
      try {
        const response = await axios.get('/api/makers');
        // Filter for likely ICE makers (non-EV) for better user experience
        const iceMakers = response.data.filter(maker => 
          !maker.toLowerCase().includes('electric') && 
          !maker.toLowerCase().includes('ev') &&
          !maker.toLowerCase().includes('battery') &&
          !maker.toLowerCase().includes('ather') &&
          !maker.toLowerCase().includes('ola')
        );
        setAllMakers(iceMakers);
      } catch (error) {
        console.error('Error fetching makers:', error);
      }
    };
    
    fetchMakers();
  }, []);

  // Fetch data based on filters
  useEffect(() => {
    const fetchData = async () => {
      try {
        setIsLoading(prev => ({ 
          ...prev, 
          iceMakers: true,
          monthlyTrend: true,
          tableData: true,
          makerMonthly: filters.maker ? true : prev.makerMonthly
        }));

        const requests = [
          axios.get('/api/sales/ice', { 
            params: { 
              year: filters.year, 
              state: filters.state
            } 
          }),
          axios.get('/api/sales/monthly', { 
            params: { 
              year: filters.year, 
              state: filters.state,
              type: 'ice'
            } 
          })
        ];
        
        // Add maker monthly data request if a maker is selected
        if (filters.maker) {
          requests.push(
            axios.get('/api/sales/maker-monthly', {
              params: {
                year: filters.year,
                state: filters.state,
                maker: filters.maker,
                type: 'ice'
              }
            })
          );
        }

        const responses = await Promise.all(requests);
        
        setIceData(responses[0].data);
        setMonthlyData(responses[1].data);
        
        if (filters.maker && responses[2]) {
          setMakerMonthlyData(responses[2].data);
        }
      } catch (error) {
        console.error('Error fetching data:', error);
      } finally {
        setIsLoading(prev => ({ 
          ...prev, 
          iceMakers: false,
          monthlyTrend: false,
          tableData: false,
          makerMonthly: filters.maker ? false : prev.makerMonthly
        }));
      }
    };

    fetchData();
  }, [filters]);

  // Fetch comparison data when selected makers change
  useEffect(() => {
    const fetchComparisonData = async () => {
      if (selectedMakers.length === 0) {
        setCompareData([]);
        return;
      }
      
      try {
        setIsLoading(prev => ({ ...prev, comparison: true }));
        
        const response = await axios.get('/api/sales/compare-makers', {
          params: {
            year: filters.year,
            state: filters.state,
            makers: selectedMakers.join(','),
            type: 'ice'
          }
        });
        
        setCompareData(response.data);
      } catch (error) {
        console.error('Error fetching comparison data:', error);
      } finally {
        setIsLoading(prev => ({ ...prev, comparison: false }));
      }
    };
    
    fetchComparisonData();
  }, [selectedMakers, filters.year, filters.state]);

  const handleFilterChange = (newFilters) => {
    setFilters(prevFilters => ({
      ...prevFilters,
      ...newFilters
    }));
    setPage(0);  // Reset to first page whenever filters change
    setExpanded(false); // Collapse filter panel after applying filters
    
    // Scroll to top of content
    setTimeout(() => {
      if (contentRef.current) {
        contentRef.current.scrollTop = 0;
      }
    }, 100);
  };

  const handleChangePage = (event, newPage) => {
    setPage(newPage);
  };

  const handleChangeRowsPerPage = (event) => {
    setRowsPerPage(parseInt(event.target.value, 10));
    setPage(0);
  };
  
  const handleTabChange = (event, newValue) => {
    setTabValue(newValue);
  };
  
  const handleViewTypeChange = (type) => {
    setViewType(type);
  };
  
  const handleAddMaker = (maker) => {
    if (maker && !selectedMakers.includes(maker)) {
      setSelectedMakers([...selectedMakers, maker]);
    }
  };
  
  const handleRemoveMaker = (maker) => {
    setSelectedMakers(selectedMakers.filter(m => m !== maker));
  };

  // Prepare chart data
  const iceMakersChartData = {
    labels: iceData.slice(0, 10).map(item => item._id),
    datasets: [
      {
        label: 'ICE Sales',
        data: iceData.slice(0, 10).map(item => item.totalSales),
        backgroundColor: 'rgba(255, 99, 132, 0.6)',
        borderColor: 'rgba(255, 99, 132, 1)',
        borderWidth: 1
      }
    ]
  };

  const monthlyTrendChartData = {
    labels: monthlyData.map(item => item.month.substring(0, 3)),
    datasets: [
      {
        label: 'Monthly ICE Sales',
        data: monthlyData.map(item => item.sales),
        fill: false,
        backgroundColor: 'rgba(255, 99, 132, 0.6)',
        borderColor: 'rgba(255, 99, 132, 1)',
        tension: 0.1
      }
    ]
  };
  
  // Prepare maker monthly chart data
  const makerMonthlyChartData = {
    labels: makerMonthlyData.map(item => item.shortMonth),
    datasets: [
      {
        label: `${filters.maker || 'Selected Maker'} Monthly Sales`,
        data: makerMonthlyData.map(item => item.sales),
        fill: false,
        backgroundColor: 'rgba(75, 192, 192, 0.6)',
        borderColor: 'rgba(75, 192, 192, 1)',
        tension: 0.1
      }
    ]
  };
  
  // Prepare comparison chart data
  const comparisonChartData = {
    labels: compareData.length > 0 && compareData[0].monthlyData 
      ? compareData[0].monthlyData.map(item => item.month) 
      : [],
    datasets: compareData.map((maker, index) => {
      // Generate a different color for each maker
      const hue = (index * 137) % 360; // Golden angle approximation for better color distribution
      return {
        label: maker.maker,
        data: maker.monthlyData ? maker.monthlyData.map(item => item.sales) : [],
        backgroundColor: `hsla(${hue}, 70%, 60%, 0.6)`,
        borderColor: `hsla(${hue}, 70%, 50%, 1)`,
        tension: 0.1
      };
    })
  };
  
  // Tab content for maker comparison
  const ComparisonTabContent = () => (
    <Box sx={{ p: 2 }}>
      <Box sx={{ mb: 3, display: 'flex', alignItems: 'center', flexWrap: 'wrap', gap: 1 }}>
        <Autocomplete
          sx={{ width: 300 }}
          options={allMakers.filter(maker => !selectedMakers.includes(maker))}
          renderInput={(params) => <TextField {...params} label="Add Maker to Compare" />}
          onChange={(event, newValue) => handleAddMaker(newValue)}
          disableClearable
        />
        <Button 
          variant="contained" 
          color="primary" 
          startIcon={<AddIcon />}
          disabled={selectedMakers.length >= 5}
          onClick={() => document.querySelector('.MuiAutocomplete-popupIndicator').click()}
          sx={{ height: 56 }}
        >
          Add to Compare
        </Button>
        
        <Box sx={{ ml: 'auto', display: 'flex', gap: 1 }}>
          <Tooltip title="Bar Chart">
            <IconButton 
              color={viewType === 'bar' ? 'primary' : 'default'} 
              onClick={() => handleViewTypeChange('bar')}
            >
              <BarChartIcon />
            </IconButton>
          </Tooltip>
          <Tooltip title="Line Chart">
            <IconButton 
              color={viewType === 'line' ? 'primary' : 'default'} 
              onClick={() => handleViewTypeChange('line')}
            >
              <TimelineIcon />
            </IconButton>
          </Tooltip>
        </Box>
      </Box>
      
      <Box sx={{ mb: 2, display: 'flex', flexWrap: 'wrap', gap: 1 }}>
        {selectedMakers.map(maker => (
          <Chip 
            key={maker}
            label={maker}
            onDelete={() => handleRemoveMaker(maker)}
            deleteIcon={<ClearIcon />}
            sx={{ 
              bgcolor: alpha(theme.palette.primary.main, 0.1),
              '&:hover': {
                bgcolor: alpha(theme.palette.primary.main, 0.2),
              }
            }}
          />
        ))}
      </Box>
      
      {selectedMakers.length === 0 ? (
        <Box 
          sx={{ 
            height: 300, 
            display: 'flex', 
            alignItems: 'center', 
            justifyContent: 'center',
            flexDirection: 'column',
            gap: 2,
            bgcolor: 'background.paper',
            borderRadius: 1,
            p: 3,
            textAlign: 'center'
          }}
        >
          <CompareArrowsIcon sx={{ fontSize: 48, color: 'text.secondary' }} />
          <Typography variant="h6" color="text.secondary">
            Add makers to compare their monthly sales data
          </Typography>
          <Typography variant="body2" color="text.secondary">
            You can compare up to 5 makers at once
          </Typography>
        </Box>
      ) : (
        <Box sx={{ height: 400 }}>
          {viewType === 'bar' ? (
            <Bar 
              ref={comparisonChartRef}
              data={comparisonChartData}
              options={{
                responsive: true,
                maintainAspectRatio: false,
                scales: {
                  y: {
                    beginAtZero: true,
                    ticks: {
                      callback: function(value) {
                        return value.toLocaleString();
                      }
                    }
                  }
                },
                plugins: {
                  tooltip: {
                    callbacks: {
                      label: function(context) {
                        return `${context.dataset.label}: ${context.raw.toLocaleString()}`;
                      }
                    }
                  }
                }
              }}
            />
          ) : (
            <Line 
              ref={comparisonChartRef}
              data={comparisonChartData}
              options={{
                responsive: true,
                maintainAspectRatio: false,
                scales: {
                  y: {
                    beginAtZero: true,
                    ticks: {
                      callback: function(value) {
                        return value.toLocaleString();
                      }
                    }
                  }
                },
                plugins: {
                  tooltip: {
                    callbacks: {
                      label: function(context) {
                        return `${context.dataset.label}: ${context.raw.toLocaleString()}`;
                      }
                    }
                  }
                }
              }}
            />
          )}
        </Box>
      )}
      
      {selectedMakers.length > 0 && (
        <TableContainer component={Paper} sx={{ mt: 3, maxHeight: 440 }}>
          <Table stickyHeader aria-label="comparison table">
            <TableHead>
              <TableRow>
                <TableCell sx={{ fontWeight: 'bold' }}>Maker</TableCell>
                <TableCell sx={{ fontWeight: 'bold' }} align="right">Total Sales</TableCell>
                {compareData.length > 0 && compareData[0].monthlyData && 
                  compareData[0].monthlyData.map(item => (
                    <TableCell key={item.month} sx={{ fontWeight: 'bold' }} align="right">
                      {item.month}
                    </TableCell>
                  ))
                }
              </TableRow>
            </TableHead>
            <TableBody>
              {compareData.map((maker) => (
                <TableRow key={maker.maker}>
                  <TableCell component="th" scope="row">{maker.maker}</TableCell>
                  <TableCell align="right">{maker.totalSales.toLocaleString()}</TableCell>
                  {maker.monthlyData && maker.monthlyData.map(item => (
                    <TableCell key={`${maker.maker}-${item.month}`} align="right">
                      {item.sales.toLocaleString()}
                    </TableCell>
                  ))}
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </TableContainer>
      )}
    </Box>
  );

  // Function to optimize table data display
  const optimizeTableDisplay = () => {
    return iceData
      .slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage)
      .map((row) => (
        <TableRow 
          key={row._id}
          hover
          sx={{ 
            '&:nth-of-type(odd)': { 
              backgroundColor: theme.palette.action.hover 
            }
          }}
        >
          <TableCell component="th" scope="row" sx={{ py: 1, fontSize: '0.875rem' }}>
            {row._id}
          </TableCell>
          <TableCell align="right" sx={{ py: 1, fontSize: '0.875rem' }}>
            {row.totalSales.toLocaleString()}
          </TableCell>
        </TableRow>
      ));
  };

  // Add this function near the top with other functions
  const downloadCSV = (data) => {
    // Create CSV content
    const headers = ['Rank', 'Maker', 'Total Sales'];
    const csvContent = [
      headers.join(','),
      ...data.map((row, index) => [
        index + 1,
        row._id,
        row.totalSales
      ].join(','))
    ].join('\n');

    // Create and trigger download
    const blob = new Blob([csvContent], { type: 'text/csv;charset=utf-8;' });
    const link = document.createElement('a');
    const url = URL.createObjectURL(blob);
    link.setAttribute('href', url);
    link.setAttribute('download', `ice_makers_sales_${filters.year || 'all'}.csv`);
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
  };

  return (
    <Box sx={{ 
      height: '100vh', 
      display: 'flex', 
      flexDirection: 'column',
      overflow: 'hidden',
      pl: 0,
      ml: 0
    }}>
      <Box sx={{
        position: 'sticky', 
        top: 0, 
        zIndex: 10,
        backgroundColor: theme.palette.background.default,
        borderBottom: `1px solid ${alpha(theme.palette.divider, 0.1)}`
      }}>
        <Typography variant="h5" sx={{ px: 2, pt: 2, pb: 1 }}>
          ICE Analytics
        </Typography>
        
        <Box>
          <FilterPanel 
            onFilterChange={handleFilterChange} 
            defaultFilters={{ vehicleType: 'ice' }}
            showTypeFilter={false}
            expanded={expanded}
            setExpanded={setExpanded}
          />
        </Box>
        
        <Box sx={{ px: 2, borderBottom: 1, borderColor: 'divider' }}>
          <Tabs
            value={tabValue}
            onChange={handleTabChange}
            aria-label="ICE analytics tabs"
          >
            <Tab label="Overview" />
            <Tab label="Compare Makers" />
          </Tabs>
        </Box>
      </Box>
      
      <Box 
        ref={contentRef}
        sx={{ 
          flexGrow: 1, 
          overflow: 'auto',
          px: 2,
          pt: 2,
          pb: 4,
          '::-webkit-scrollbar': {
            width: '8px'
          },
          '::-webkit-scrollbar-track': {
            background: 'transparent'
          },
          '::-webkit-scrollbar-thumb': {
            background: alpha(theme.palette.primary.main, 0.1),
            borderRadius: '4px'
          },
          '::-webkit-scrollbar-thumb:hover': {
            background: alpha(theme.palette.primary.main, 0.2)
          }
        }}
      >
        {tabValue === 0 ? (
          <Grid container spacing={2}>
            {/* Top ICE Makers Chart */}
            <Grid item xs={12} md={6}>
              <ChartContainer 
                title="Top 10 ICE Makers by Sales" 
                isLoading={isLoading.iceMakers}
                chartRef={!isLoading.iceMakers ? iceMakersChartRef : null}
                chartId="top-ice-makers"
                chartType="bar"
              >
                <Bar 
                  ref={iceMakersChartRef}
                  data={iceMakersChartData} 
                  options={{
                    responsive: true,
                    maintainAspectRatio: false,
                    plugins: {
                      legend: {
                        display: false,
                      },
                      tooltip: {
                        callbacks: {
                          label: function(context) {
                            return `Sales: ${context.raw.toLocaleString()}`;
                          }
                        }
                      }
                    },
                    scales: {
                      y: {
                        beginAtZero: true,
                        ticks: {
                          callback: function(value) {
                            return value.toLocaleString();
                          }
                        }
                      }
                    }
                  }} 
                />
              </ChartContainer>
            </Grid>
            
            {/* Monthly Trend Chart */}
            <Grid item xs={12} md={6}>
              <ChartContainer 
                title="Monthly ICE Sales Trend" 
                isLoading={isLoading.monthlyTrend}
                chartRef={!isLoading.monthlyTrend ? monthlyTrendChartRef : null}
                chartId="monthly-ice-trend"
                chartType="line"
              >
                <Line 
                  ref={monthlyTrendChartRef}
                  data={monthlyTrendChartData} 
                  options={{
                    responsive: true,
                    maintainAspectRatio: false,
                    plugins: {
                      legend: {
                        display: false,
                      },
                      tooltip: {
                        callbacks: {
                          label: function(context) {
                            return `Sales: ${context.raw.toLocaleString()}`;
                          }
                        }
                      }
                    },
                    scales: {
                      y: {
                        beginAtZero: true,
                        ticks: {
                          callback: function(value) {
                            return value.toLocaleString();
                          }
                        }
                      }
                    }
                  }} 
                />
              </ChartContainer>
            </Grid>
            {/* Maker Monthly Chart - Only show if a maker is selected */}
            {filters.maker && (
              <Grid item xs={12}>
                <ChartContainer 
                  title={`Monthly Sales for ${filters.maker}`}
                  isLoading={isLoading.makerMonthly}
                  chartRef={!isLoading.makerMonthly ? makerMonthlyChartRef : null}
                  chartId={`maker-${filters.maker.replace(/\s+/g, '-').toLowerCase()}`}
                  chartType="line"
                >
                  <Line 
                    ref={makerMonthlyChartRef}
                    data={makerMonthlyChartData}
                    options={{
                      responsive: true,
                      maintainAspectRatio: false,
                      plugins: {
                        legend: {
                          display: false,
                        },
                        tooltip: {
                          callbacks: {
                            label: function(context) {
                              return `Sales: ${context.raw.toLocaleString()}`;
                            }
                          }
                        }
                      },
                      scales: {
                        y: {
                          beginAtZero: true,
                          ticks: {
                            callback: function(value) {
                              return value.toLocaleString();
                            }
                          }
                        }
                      }
                    }}
                  />
                </ChartContainer>
              </Grid>
            )}
            
            {/* Data Table */}
            <Grid item xs={12}>
              <ChartContainer 
                title="All ICE Makers Sales Data" 
                isLoading={isLoading.tableData}
                height="auto"
              >
                {/* Summary Section */}
                <Box sx={{ 
                  display: 'flex', 
                  gap: 4, 
                  mb: 2, 
                  p: 2, 
                  bgcolor: 'background.paper',
                  borderRadius: 1,
                  justifyContent: 'space-between',
                  alignItems: 'center'
                }}>
                  <Box sx={{ display: 'flex', gap: 4 }}>
                    <Box>
                      <Typography color="primary" variant="subtitle2">
                        Total ICE Makers
                      </Typography>
                      <Typography variant="h6">
                        {iceData.length}
                      </Typography>
                    </Box>
                    <Box>
                      <Typography color="primary" variant="subtitle2">
                        Total Sales
                      </Typography>
                      <Typography variant="h6">
                        {iceData.reduce((sum, item) => sum + item.totalSales, 0).toLocaleString()}
                      </Typography>
                    </Box>
                    <Box>
                      <Typography color="primary" variant="subtitle2">
                        Top 10 Market Share
                      </Typography>
                      <Typography variant="h6">
                        {((iceData.slice(0, 10).reduce((sum, item) => sum + item.totalSales, 0) / 
                          iceData.reduce((sum, item) => sum + item.totalSales, 0)) * 100).toFixed(1)}%
                      </Typography>
                    </Box>
                  </Box>
                  <Button
                    variant="outlined"
                    startIcon={<DownloadIcon />}
                    onClick={() => downloadCSV(iceData)}
                    sx={{ height: 40 }}
                  >
                    Download
                  </Button>
                </Box>

                <TableContainer sx={{ 
                  height: 'calc(100vh - 400px)', // Adjust height to fit screen
                  overflow: 'auto',
                  '::-webkit-scrollbar': {
                    width: '8px',
                    height: '8px'
                  },
                  '::-webkit-scrollbar-track': {
                    background: 'transparent'
                  },
                  '::-webkit-scrollbar-thumb': {
                    background: alpha(theme.palette.primary.main, 0.1),
                    borderRadius: '4px'
                  },
                  '::-webkit-scrollbar-thumb:hover': {
                    background: alpha(theme.palette.primary.main, 0.2)
                  }
                }}>
                  <Table stickyHeader size="small" aria-label="ICE sales table">
                    <TableHead>
                      <TableRow>
                        <TableCell sx={{ fontWeight: 'bold', backgroundColor: theme.palette.primary.light, color: 'white' }}>Rank</TableCell>
                        <TableCell sx={{ fontWeight: 'bold', backgroundColor: theme.palette.primary.light, color: 'white' }}>Maker</TableCell>
                        <TableCell sx={{ fontWeight: 'bold', backgroundColor: theme.palette.primary.light, color: 'white' }} align="right">Total Sales</TableCell>
                        <TableCell sx={{ fontWeight: 'bold', backgroundColor: theme.palette.primary.light, color: 'white' }} align="center">Actions</TableCell>
                      </TableRow>
                    </TableHead>
                    <TableBody>
                      {iceData.map((row, index) => (
                        <TableRow 
                          key={row._id}
                          hover
                          sx={{ 
                            '&:nth-of-type(odd)': { 
                              backgroundColor: theme.palette.action.hover 
                            }
                          }}
                        >
                          <TableCell sx={{ py: 1, fontSize: '0.875rem' }}>
                            {index + 1}
                          </TableCell>
                          <TableCell component="th" scope="row" sx={{ py: 1, fontSize: '0.875rem' }}>
                            {row._id}
                          </TableCell>
                          <TableCell align="right" sx={{ py: 1, fontSize: '0.875rem' }}>
                            {row.totalSales.toLocaleString()}
                          </TableCell>
                          <TableCell align="center" sx={{ py: 1 }}>
                            <Button
                              size="small"
                              variant="outlined"
                              onClick={() => handleAddMaker(row._id)}
                              disabled={selectedMakers.includes(row._id)}
                              sx={{ 
                                fontSize: '0.75rem',
                                py: 0.5
                              }}
                            >
                              Compare
                            </Button>
                          </TableCell>
                        </TableRow>
                      ))}
                    </TableBody>
                  </Table>
                </TableContainer>
              </ChartContainer>
            </Grid>
          </Grid>
        ) : (
          <ChartContainer
            title="ICE Makers Comparison"
            isLoading={isLoading.comparison}
            height="auto"
            chartRef={selectedMakers.length > 0 && !isLoading.comparison ? comparisonChartRef : null}
            chartId="ice-makers-comparison"
            chartType={viewType}
          >
            <ComparisonTabContent />
          </ChartContainer>
        )}
      </Box>
    </Box>
  );
};


export default IceAnalytics; 